@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-bold-webfont.woff2") format("woff2"), url("../fonts/montserrat-bold-webfont.woff") format("woff");
  font-weight: 700;
  font-style: normal; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-regular-webfont.woff2") format("woff2"), url("../fonts/montserrat-regular-webfont.woff") format("woff");
  font-weight: 400;
  font-style: normal; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-semibold-webfont.woff2") format("woff2"), url("../fonts/montserrat-semibold-webfont.woff") format("woff");
  font-weight: 600;
  font-style: normal; }

html {
  box-sizing: border-box;
  -moz-osx-font-smoothing: grayscale; }

*,
*::before,
*::after {
  box-sizing: inherit; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

body {
  position: relative;
  padding: 0;
  height: 100%; }

.button {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  line-height: 17px;
  display: block;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  border-radius: 50px;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 14px;
  padding-bottom: 14px;
  min-height: 42px; }

.button:hover {
  opacity: 0.5; }

.button--record {
  background-color: #ffffff;
  color: #2f80ed;
  text-transform: none;
  min-width: 160px; }

.top-wrapper {
  padding-top: 33px; }

@media (max-width: 1439px) {
  .top-wrapper {
    padding-top: 40px; } }

@media (max-width: 767px) {
  .top-wrapper {
    padding-top: 29px; } }

.header {
  margin-left: auto;
  margin-right: auto;
  display: flex;
  max-width: 1175px; }

.header__cities-list {
  width: 27.4%;
  margin-right: 158px; }

.header__menu {
  display: flex;
  align-items: center;
  width: 40.17%; }

.header__button {
  margin-left: auto; }

.cities-list {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  line-height: 17px;
  display: flex;
  align-items: center;
  justify-content: space-between; }

.cities-list__item a {
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff; }

.cities-list__item a:hover {
  opacity: 0.5; }

.cities-list__item:first-child a {
  font-weight: 700;
  color: #2f80ed; }

.header-menu-list {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  line-height: 17px;
  display: flex;
  justify-content: space-between;
  width: 100%; }

.header-menu-list__item a {
  text-decoration: none;
  color: #ffffff;
  width: 100%; }

.header-menu-list__item:last-child a {
  margin-right: 0; }

.header-menu-list__item a:hover {
  text-shadow: 0 0 1.5px #ffffff, 0 0 1.5px #ffffff; }
